.faq .container {
  padding-top: 120px;
  padding-bottom: 120px;

  @include vp-1199 {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

.faq {

  width: 100%;


  h2 {
    font-family: $ff-alumni-sans;
    color: $color-sapphire;
    font-size: 54px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 1.62px;
    text-align: left;
    margin-bottom: 40px;
  }
}

.faq__tabs {
  display: flex;
  flex-direction: row;
  gap: 40px;
  font-family: $ff-roboto-condensed;
  color: $color-primary;

  @include vp-1199 {
    flex-direction: column;
  }

}

.faq__tabs-controls {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-width: 260px;
  width: 100%;
  align-items: center;

  @include vp-1199 {
    flex-direction: row;
    max-width: none;
    width: 100%;
    justify-content: space-between;
    gap: 10px;
  }

  @include vp-767 {
    gap: 8px;
    flex-wrap: wrap;
    min-width: 290px;
  }
}

.faq__tabs-control {
  color: $color-sapphire;
  background: $color-default-white;
  border: 1px solid $color-icon-dark-gradient;
  width: 100%;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%;
  text-align: center;

  &:hover,
  &:focus {
    color: $color-default-black;
    background-color: transparent;
    border: 1px solid $color-button-border-dark;
  }

  &.is-active {
    color: $color-default-white;
    background: $color-button-background-gradient;
    outline: none;
  }

  &:disabled {
    border: 1px solid rgba(16, 38, 84, 0.2);
    color: #c8c9cc;
  }

  @include vp-1199 {
    padding: 15px 57px;
    width: 162px;
  }

  @include vp-767 {
    padding: 12px 49px;
    width: 141px;
    font-size: 16px;
  }
}


.faq__tabs-content {
  width: 100%;
  border-top: 1px solid rgba(28, 51, 116, 0.20);
}